import React from 'react';
import './App.css';
import Team from './Team';
import Home from './Home';
import Head from './Head';
import Bottom from './Bottom'
import Research from './Research';
import Databases from './Databases';

import {
    BrowserRouter as Router,
    Switch,
    Route,
    HashRouter,
} from "react-router-dom";

// basename={process.env.PUBLIC_URL}

function App() {
    
    return (
	<div>
	    <div class="container justify-content-md-center">
		<HashRouter >
		    <Switch>
			<Route exact path="/">
			    <Home />
			</Route>
			<Route path="/research">
			    <Research />
			</Route>
			<Route path="/research/publications">
			    <Research />
			</Route>
			<Route path="/research/models">
			    <Developping />
			</Route>
			<Route path="/research/databases">
			    <Developping />
			</Route>
			<Route path="/industry">
			    <Developping />
			</Route>
			<Route path="/team">
			    <Team />
			</Route>
			<Route path="/corperation">
			    <Developping />
			</Route>
			<Route path="/about">
			    <About />
			</Route>
			<Route path="/Databases">
			    <Databases />
			</Route>
		    </Switch>
		</HashRouter>
	    </div>
	    <Bottom />
	</div>
    );

}



function About() {
    return (
	<div>
	    <Head />
	    <h2>About</h2>
	</div>
    );
}

function Developping() {
    return (
	<div>
	    <Head />
	    <h1>Developing...</h1>	    
	</div>
    );
}

export default App;
